.advantages-section {
  background-color: $light-section-color;
  padding-top: 20px;
  padding-bottom: 97px;
  @media screen and (min-width: 768px) {
    padding-top: 33px;
    padding-bottom: 140px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 19px;
    padding-bottom: 139px;
  }
}
.advantages-container {
  padding-left: 17px;
  @media screen and (min-width: 768px) {
    padding-left: 36px;
  }
  @media screen and (min-width: 1280px) {
    padding-left: 115px;
  }
}
.advantages-list {
  @media screen and (min-width: 768px) {
    display: flex;
    margin-bottom: 0;
  }
  @media screen and (min-width: 1280px) {
  }
}
.advantages-item {
  @media screen and (max-width: 767px) {
    max-width: 230px;
  }
  @media screen and (min-width: 768px) {
    min-width: 180px;
    margin-bottom: 0;
  }
  @media screen and (min-width: 1280px) {
    min-width: 330px;
  }
  &:not(:last-child) {
    margin-bottom: 38px;
    @media screen and (min-width: 768px) {
      margin-right: 60px;
      margin-bottom: 0;
    }
    @media screen and (min-width: 1280px) {
      margin-right: 30px;
      margin-bottom: 0;
    }
  }

  &:nth-child(1)::before {
    content: '';
    display: block;
    margin-bottom: 16px;
    width: 33px;
    height: 50px;
    background-image: url(../images/advantages/icon-milk.svg);
    @media screen and (min-width: 768px) {
      margin-bottom: 36px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 40px;
    }
  }

  &:nth-child(2)::before {
    content: '';
    display: block;
    margin-bottom: 16px;
    width: 50px;
    height: 50px;
    background-image: url(../images/advantages/icon-fruit.svg);
    @media screen and (min-width: 768px) {
      margin-bottom: 36px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 40px;
    }
  }

  &:nth-child(3)::before {
    content: '';
    display: block;
    margin-bottom: 16px;
    width: 44px;
    height: 50px;
    background-image: url(../images/advantages/icon-icecream.svg);
    @media screen and (min-width: 768px) {
      margin-bottom: 36px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 40px;
    }
  }
}
